.ct-white-label-settings {

	.ct-layer-controls span {
		font-size: 13px;
		cursor: pointer;
	}

	.ct-layer-content {
		padding-bottom: 25px;
	}

	section:first-of-type {
		padding-top: 20px;
		margin-top: 20px;
		border-top: 1px dashed #eee;
	}
}

.ct-white-label-group {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin-top: 25px;

	> div {
		display: flex;
		flex-direction: column;

		&:not(.half-size) {
			width: 100%;
		}

		&.half-size {
			width: 48%;
		}

		&:not(:last-child) {
			margin-bottom: 20px;
		}
	}

	label {
		margin-bottom: 5px;
		font-size: 12px;
		font-weight: 500;
	}

	textarea {
		min-height: 70px;
	}
}


.ct-upload-thumb {
	display: flex;
	flex-wrap: wrap;

	input {
		flex: 1;
		margin-right: 10px;
	}

	span {
		font-size: 12px;
		font-style: italic;
		width: 100%;
		margin-top: 5px;
		opacity: 0.5;
	}

	button {
		--buttonHeight: 30px;
	}
}

.ct-white-label-actions-group {
	padding-top: 20px;
	margin-top: 20px;
	border-top: 1px dashed #eee;

	label {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 20px;
	}
}

.ct-white-label-save {
	padding-top: 20px;
	margin-top: 20px;
	border-top: 1px dashed #eee;

	.ct-button-primary {
		--buttonHeight: 42px;
		--buttonPadding: 20px;
		--buttonFontSize: 14px;

		width: 100%;
	}
}